import React, { useEffect, useState, useRef } from "react";

// import { PDFDocument, StandardFonts, rgb } from 'pdf-lib'

import { getStore, updStore, gen16CodeId, getLabelTranslate,ObjectKeys, parseFloatToDecimal, parseDate, genAlphCode, copyToClipboard } from "../../../stores/Utils";

import {  SvgIcon_Cmpt, AccountLabelDetail_Cmpt } from "../../../components";


import "./styles.css"

import { useNavigate } from "react-router";

import { InventoryLabelsPdf } from "../services/export_service";
import { getall_subaccount } from "../../CuentasContables/services/server_fetching";
import { getall_provider } from "../../ProvidersCustomers/services/server_fetching";
import { entry_invoice_acc_2, getall_product, clearComprobante } from "../services/server_fetching";
import { CloseModal } from "../../../components/DialogHRM";
import { OpenToast } from "../../../components/Toast";





const Svg = SvgIcon_Cmpt();


const AccountLabelDetail = AccountLabelDetail_Cmpt();



const frmNm = "view_product_entry_comprobante"

const AddEntryProduct  = ({ data, confirm, item }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	const navigate = useNavigate();

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			updStore("accountPicker", {});
			set_obs(gen16CodeId())

			if(!getStore("account_ids")){
				getall_subaccount(updObs);
			}
			if(!getStore("providers_ids")?.[ item?.providerId]?.name){
				getall_provider(updObs)
			}
			if(!getStore("products_ids")){
				getall_product(updObs)
			}
			
		}
	},[item.entriesInvoiceId]);


	const updObs  = async ()  => {
		set_obs(gen16CodeId())
	};

	const handleClose =  (td) => {

		confirm && confirm();
		data.modalID && CloseModal({id:data.modalID});
		td.data.comprobanteId && 
		navigate({
			pathname: "/accounting-ledger" ,
			search: `?cId=${td.data.comprobanteId}`
		});
    }


	const send2Accountant  = async ()  => {
		entry_invoice_acc_2( item?.entriesInvoiceId,handleClose);
	};

	const clearComp  = async ()  => {
		// clearComprobante( item?.entriesInvoiceId,updObs);
	};

	// 2023-12747-RDU
	// Compra de Materiales a Stonebasyx Inv.2023-12747-RDU

	// luna  347.95  7.95
	// gold  176.04  19.95

	let customer = getStore("providers_ids")?.[ item?.providerId]?.name;
	
	
	const copyToClpbCB  =  ()  => {
		//OpenToast({ text: "Copied to clipobard", timeout: 1500 });
	};
	const copyToClpb  =  (v)  => {
		copyToClipboard( v,copyToClpbCB);
	};

	return (
		<div style={{
			maxHeight: "90vh",
			minHeight:  "88vh",
			width: "860px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
					Entrada de Mercancias
				</p>
				<div className="flexSpace"/>
				<div>

					<div className="date2Upd" >
						{ parseDate(getStore(frmNm)?.["date"])}
					</div>
				</div>
			</div>

			<div className="_dsplFlx ">
				
				<div className=" ">
					<p className="label">
						{"Pay to"}
					</p>
					<div className="date2Upd" >
						{ customer}
					</div>
				</div>
				<div className="flexSpace"/>
				<div>
					<p className="label">
						{"Documento"}
					</p>
					<div className="date2Upd" onClick={()=>copyToClpb(getStore(frmNm)?.["document"])} >
						{ getStore(frmNm)?.["document"]}
					</div>
				</div>
			</div>
			<div className="_dsplFlx ">
				<div className=" ">
					<p className="label">
						{"Descripcion"}
					</p>
					<div className="value_desc"  onClick={()=>copyToClpb(getStore(frmNm)?.["description"])}>
						{ getStore(frmNm)?.["description"]}
					</div>
				</div>
				<div className="flexSpace"/>
			</div>
			<div className="_dsplFlx ">
				<div className="flexSpace"/>
				{ getStore(frmNm)?.["comprobanteId"]?
					<div className=" "  onClick={clearComp}>
						<p className="label">
							{"Comprobante"}
						</p>
						<div className="date2Upd" >
							{ getStore(frmNm)?.["comprobanteId"]}
						</div>
					</div>:
					<p className="group_title addBtn purple" onClick={send2Accountant} >
						Enviar a contabilidad
					</p>
				}
			</div>
			

			
			<ProductsBox data={item.inventoryEntries}/>

			<ExtraExpenses data={item.extrasEntries} />

			
			<div className="_dsplFlx ">
				<div className="flexSpace"/>
				
			</div>
		

			
		</div>
	);
};

export default AddEntryProduct;





const EstaCuadrado = (obj) => {
	let balance = 0;
	let arr = obj && ObjectKeys(obj);
	arr && arr?.map((element, indE)=>{
		let data = obj[element];
		let newB = balance.toFixed(2);
		let amount = data?.amount ? data?.type === "debit" ?  parseFloat(data.amount) :parseFloat(data.amount) *-1  : 0;
		balance = amount + parseFloat(newB);
		
	});
	return arr && arr?.length>0 && balance === 0;
}  







