const ProductsBox = ({data}) => {
	const [obs, set_obs] = useState(0);

	const printInvLbs = () => {
		InventoryLabelsPdf(data)
	}
	

	return (
		
		<div className="boxAddAcc" style={{margin: "18px 0"}}>

			
			<div className="_dsplFlx hdr_bar purple" style={{margin: "18px 0"}}>
				<div className="tr_bar tr_bar_40 left_alg">
					Producto
				</div>
				<div className="tr_bar tr_bar_10">
					ID
				</div>
				<div className="tr_bar tr_bar_15">
					Dimension
				</div>
				<div className="tr_bar tr_bar_15">
					Qty
				</div>
				<div className="tr_bar tr_bar_15">
					Precio
				</div>
				<div className="tr_bar tr_bar_5">
					<div className="icon" onClick={printInvLbs}>
						<Svg name={"printer"} color={"#646cff"}/>
					</div>
				</div>
			</div>
			{data && data.length > 0  && 
				data?.map((element, indE)=>
				<ListingProducts 
					key={element.productId +"_"+ indE} 
					idKey={element.productId}
					index={indE} 
					data={element} 
				/>
			)}
		</div>

	)
}






const ListingProducts  = ({ data,  index }) => {

	let productD = getStore("products_ids")?.[data?.productId];
	
	let dim = data?.dimension && data?.dimension.split(",")[0] +" X " + data?.dimension.split(",")[1];
	
	let dimenss =  data?.dimension && {
		lbl: dim + " (inch)",
		hover: data?.squareFeet + "S/F X $" +  parseFloatToDecimal(data?.pricePerSquareFeet,2)
	};

	const copyToClpbCB  =  ()  => {
		OpenToast({ text: "Copied to clipobard", timeout: 1500 });
	};
	const copyToClpb  =  (v)  => {
		copyToClipboard( v,copyToClpbCB);
	};



	return (
		<div className="_dsplFlx hdr_bar">
			
			<div className="tr_bar tr_bar_40 left_alg" >
				{productD?.name}
			</div>
			<div className="tr_bar tr_bar_10 left_alg date2Upd" >
				{data?.alphaCode}
			</div>
			<div className="tr_bar tr_bar_15" style={{marginTop:-4}}>
			{data?.dimension?
				<AccountLabelDetail 
					data={dimenss} 
					color={"#282828"}
					labelField={"lbl"}
					infoField={"hover"}
				/>
				:null}
			</div>
			<div className="tr_bar tr_bar_15"  onClick={()=>copyToClpb(parseFloatToDecimal(data?.qty,2))}>
				{parseFloatToDecimal(data?.qty,2)}
			</div>
			<div className="tr_bar tr_bar_15"  onClick={()=>copyToClpb(parseFloatToDecimal(data?.price,2))}>
				{parseFloatToDecimal(data?.price,2)}
			</div>
			<div className="flexSpace"/>
			

		</div>
	)

}










