const ExtraExpenses = ({data}) => {


	return (
		
		<div className="boxAddAcc" style={{margin: "18px 0"}}>

			
			<div className="_dsplFlx hdr_bar purple" style={{margin: "18px 0"}}>
				<div className="tr_bar tr_bar_25">
					Cuenta
				</div>
				<div className="tr_bar tr_bar_25">
					Sub Cuenta
				</div>
				<div className="tr_bar tr_bar_25">
				
				</div>
				<div className="tr_bar tr_bar_25">
					Monto
				</div>
			</div>
			{data && data?.map((element, indE)=>
				<ListingAccounts 
					key={element.operationId} 
					idKey={element.operationId} 
					data={element} 
				/>
			)}
		</div>

	)
}




const ListingAccounts  = ({ data }) => {

	let accountD = getStore("account_ids")?.[data?.account];
	let subaccountD = getStore("sub_account_ids")?.[data?.subAccount];

	
	return (
		<div className="_dsplFlx hdr_bar">
			<div className="tr_bar tr_bar_25">
				<AccountLabelDetail 
					data={accountD}
					labelField={"name"}
					infoField={"code"}
				/>
			</div>
			<div className="tr_bar tr_bar_25">
				<AccountLabelDetail 
					data={subaccountD}
					labelField={"subAccountName"}
					infoField={"subAccount"}
				/>
			</div>
			<div className="tr_bar tr_bar_25">
				
			</div>
			<div className="tr_bar tr_bar_25">
				{data?.type === "debit" ? parseFloatToDecimal(data?.amount,2) : "0.00" }
			</div>
			<div className="flexSpace"/>
			

		</div>
	)

}
































  
  




