import { PDFDocument, StandardFonts, rgb, degrees } from 'pdf-lib'
import { getStore, blobfromArray, parseDateMMDDYY, getQrcode } from '../../../stores/Utils';

import fontkit from '@pdf-lib/fontkit';




export const InventoryLabelsPdf = async (list) => {
    
  

	const pdfDoc = await PDFDocument.create() 
	pdfDoc.registerFontkit(fontkit);

  


	var timesRomanFont = await pdfDoc.embedFont(StandardFonts.TimesRoman);
	var TimesRomanBold = await pdfDoc.embedFont(StandardFonts.TimesRomanBold);

if(getStore("modernFontReport")){
	let fontUrl = "https://qvamarkets.com/gql_api/getStatic?fileName=Roboto-Bold.ttf"
	const fontBytes =  await fetch(fontUrl).then((res) => res.arrayBuffer());
	TimesRomanBold = await pdfDoc.embedFont(fontBytes);



	let fontUrlRoboto_italic = "https://qvamarkets.com/gql_api/getStatic?fileName=gSans-Italic.otf"
  	const fontBytes2 =  await fetch(fontUrlRoboto_italic).then((res) => res.arrayBuffer());
	timesRomanFont = await pdfDoc.embedFont(fontBytes2);
}
   
	// page225X400 = [288, 162]
	

	const promises = list.map(async (item, ind2) => {
			const page = pdfDoc.addPage([288, 162]);
			const { width, height } = page.getSize();
			let heightPg = height - 14;
			const pngUrl = getQrcode(item?.inventoryId+"");
			const pngImageBytes = await fetch(pngUrl).then((res) => res.arrayBuffer())
			let pngImage = await pdfDoc.embedPng(pngImageBytes);
			pngImage && page.drawImage(pngImage, {
				x: 10,
				y: 80,
				width:  80,
				height: 80,
			})
			

			
	
	
	/**************************************************************************/
	/**************************************************************************/
	/**************************************************************************/
	/**************************************************************************/
	/**************************************************************************/
	
		const invoiceWidth = TimesRomanBold.widthOfTextAtSize(item?.inventoryId+"" , 12);
	
		const invoiceLblWidth = timesRomanFont.widthOfTextAtSize("ID: " , 11);
	
		page.drawText("ID: " , {
			x: 270 - invoiceWidth - invoiceLblWidth,
			y:  heightPg,
			size: 11,
			font: timesRomanFont,
			color: rgb(0, 0, 0),
		})
		
		page.drawText( item?.inventoryId + "", {
			x: 270 - invoiceWidth,
			y:  heightPg,
			size: 12,
			font: TimesRomanBold,
			color:  rgb(0, 0, 0),
		})
	
	
		heightPg = heightPg -20;
	
		let dim = item?.dimension ? item?.dimension.split(",")[0] +" X " + item?.dimension.split(",")[1] +" inch":"";
		
		const docWoWidth = TimesRomanBold.widthOfTextAtSize(dim + "", 19);
	
		page.drawText(dim + ""  , {
			x: 270 - docWoWidth,
			y:  heightPg -24,
			size: 19,
			font: TimesRomanBold,
			color: rgb(0.06, 0.06, 0.06),
		})

		let sqft = item?.squareFeet ?item?.squareFeet + " S/F":"1 Unit";
		
		const sqftWoWidth = TimesRomanBold.widthOfTextAtSize(sqft + "", 19);
	
		page.drawText(sqft + ""  , {
			x: 270 - sqftWoWidth,
			y:  heightPg -44,
			size: 19,
			font: TimesRomanBold,
			color: rgb(0.06, 0.06, 0.06),
		})
		
		heightPg = heightPg -65;
		
		page.drawRectangle({
				x: 15,
				y: heightPg + 1,
				width: 560,
				height: 0.85,
				color: rgb(0.53, 0.53, 0.53),
				borderColor: rgb(0.53, 0.53, 0.53),
		});
		
		let productName = getStore("products_ids")?.[item?.productId]?.name;
			
		page.drawText(productName + "", {
				x: 20,
				y:  heightPg  -15,
				size: 14,
				font: timesRomanFont,
				color: rgb(0, 0, 0),
			})
		
		heightPg = heightPg -25;
		
		
		page.drawRectangle({
				x: 15,
				y: heightPg + 1,
				width: 560,
				height: 0.85,
				color: rgb(0.53, 0.53, 0.53),
				borderColor: rgb(0.53, 0.53, 0.53),
			});
		
	
		/**************************************************************************/
		/**************************************************************************/
		/**************************************************************************/
		/**************************************************************************/
		/**************************************************************************/
		
		heightPg = heightPg -10;
		heightPg = heightPg -20;
		
		
		
		const dateWidth = timesRomanFont.widthOfTextAtSize("Date: " , 11);
		const dateWoWidth = TimesRomanBold.widthOfTextAtSize(parseDateMMDDYY(item?.date) +"", 12);

		page.drawText("Date: " , {
			x: 270 - dateWidth - dateWoWidth,
			y:  heightPg,
			size: 11,
			font: timesRomanFont,
			color: rgb(0, 0, 0),
		})

		page.drawText(parseDateMMDDYY(item?.date) + ""  , {
			x: 270 - dateWoWidth,
			y:  heightPg,
			size: 12,
			font: TimesRomanBold,
			color: rgb(0.06, 0.06, 0.06),
		})

		page.drawText(item?.alphaCode + ""  , {
			x: 20,
			y:  heightPg+4,
			size: 22,
			font: TimesRomanBold,
			color: rgb(0.06, 0.06, 0.06),
		})

		
	})
	const contents = await Promise.all(promises);



	const pdfBytes = await pdfDoc.save();
	let blobUrl = blobfromArray(pdfBytes)
	window.open(blobUrl)

  
	
  }
  